import "./styles.scss";
import { useEffect, useState } from "react";
import { initialTeams, initialFixtures, currentVersion } from "./constans";
import {
  calcuateTeamsPointes,
  isJsonString,
  minifyJson,
  unminifyJson,
  confetti
} from "./utils";
import LeagueTable from "./components/LeagueTable";
import Fixtures from "./components/Fixtures";

export default function App() {
  const [fixtures, setFixtures] = useState(initialFixtures);
  const [teams, setTeams] = useState(
    calcuateTeamsPointes(initialTeams, initialFixtures)
  );

  useEffect(() => {
    if (
      fixtures.every((f) => f.result) &&
      (teams[0].key === "hpt" || teams[1].key === "hpt")
    ) {
      confetti({
        particleCount: 750,
        spread: 360,
        colors: ["#89CFF0", "#0047AB", "#3F00FF", "#4169E1"]
      });
    } else {
      confetti && confetti.reset();
    }
  }, [teams]);

  useEffect(() => {
    setTeams(calcuateTeamsPointes(initialTeams, fixtures));
  }, [fixtures]);

  useEffect(() => {
    try {
      const qsState = atob(
        new URL(document.location).searchParams.get("state")
      );
      const fixturesFromLS = window.localStorage.getItem("fixtures");
      if (isJsonString(qsState)) {
        applyFromClient(JSON.parse(qsState));
      } else if (isJsonString(fixturesFromLS)) {
        const LSVersion =
          window.localStorage.getItem("version") === currentVersion.toString();
        if (LSVersion && fixturesFromLS.length) {
          applyFromClient(JSON.parse(fixturesFromLS));
        }
      }
    } catch (error) {
      console.log(error);
    }
  }, []);

  const applyFromClient = (clientFixtures) => {
    const newFixtures = [...fixtures];
    unminifyJson(clientFixtures).forEach((fixture) => {
      const initialIndex = newFixtures.findIndex(
        (f) => f.home === fixture.home && f.round === fixture.round
      );
      if (initialIndex) {
        newFixtures[initialIndex].result = fixture.result;
      }
    });
    setFixtures(newFixtures);
  };

  const applyFixtureResult = (changedFixtureIdx, result) => {
    const newFixtures = [...fixtures];
    const idx = newFixtures.findIndex(
      (f) => `${f.home}/${f.away}` === changedFixtureIdx
    );
    newFixtures[idx].result =
      result === newFixtures[idx].result ? undefined : result;

    setFixtures(newFixtures);

    window.localStorage.setItem(
      "fixtures",
      minifyJson(newFixtures.filter((f) => f.result))
    );
    window.localStorage.setItem("version", currentVersion);
  };

  const shareLink = new URL(document.location);
  shareLink.searchParams.set(
    "state",
    btoa(minifyJson(fixtures.filter((f) => f.result)))
  );

  const reset = () => {
    setFixtures(
      fixtures.map((f) => {
        return { ...f, result: undefined };
      })
    );
  };

  const copyShareLink = () => {
    const isMobile = /Android|webOS|iPhone|iPad|iPod|Opera Mini/i.test(
      navigator.userAgent
    );

    if (isMobile && navigator.share) {
      navigator.share({
        text: shareLink.toString()
      });
    } else if (navigator.clipboard) {
      navigator.clipboard
        .writeText(shareLink.toString())
        .then(() => {
          alert("הועתק ללוח");
        })
        .catch((e) => console.log(e));
    } else {
      window.prompt(shareLink.toString());
    }
  };

  const shuffle = () => {
    const randomIntFromInterval = (min, max) => {
      // min and max included
      return Math.floor(Math.random() * (max - min + 1) + min);
    };

    const newFixtures = fixtures.map((f) => {
      return { ...f, result: randomIntFromInterval(1, 3).toString() };
    });
    console.table(fixtures);
    console.table(newFixtures);
    setFixtures(newFixtures);
  };

  return (
    <div className="App">
      <div id="table">
        <LeagueTable teams={teams} />
      </div>
      <button onClick={copyShareLink}>שתף מצב נוכחי</button> |
      <button onClick={reset}>איפוס</button> |
      <button onClick={shuffle}>אקראי</button>
      <div id="fixtures">
        <Fixtures fixtures={fixtures} applyFn={applyFixtureResult} />
      </div>
    </div>
  );
}
